html,body{
  color: white;
  background-color: #21232A;
  font-family: Helvetica, sans-serif;
  font-size: 1.3rem;
  padding: 20px;
 
}

button {
  font-size: 1.3rem;
  margin-right:10px ;
  padding: 10px;
}

h2{
  text-align: center;
}


input {
  width: 100%;
  font-size: 1.2rem;
}

.card{

  align-content: center;
  border: 1px solid white;
  border-radius: 6px;
  margin-bottom: 10px;
  margin-right: 10px;
  overflow: hidden;

}


.card p {
  padding:5px ;
}


.card img {
  max-height: 170px;
}

.card-grid{
  display: flex;
  flex-direction:row ;
  flex-wrap: wrap;
}